import React, { Component } from 'react';
import './About.css';
import FloatingMe from "./../../images/floating_me.jpg";

class About extends Component {

    render() {
        let sDisplayMessage = "This is the About Page";

        return (
            <div className="aboutPage">
                <div className="floatingImage">
                    <img src={FloatingMe}/>
                </div>
                <div className="description">
                    Hey there! I am Siddhant, a full time Software Developer and an aspiring UX Designer from Pune, India.<br/>
                    A balance of logical work and creative pursuits keeps me sane, hence The Logical Creative. 
                </div>
            </div>
        );
    }
}

export default About;


